
import { defineComponent } from "vue";

export default defineComponent({
  name: "Slider",
  props: {
    unit: {
      type: String,
      default: "kg",
    },
    delta: {
      type: Number,
      default: 1,
    },
    modelValue: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      value: 0,
    };
  },
  mounted() {
    this.value = this.modelValue;
    this.resize();
  },
  methods: {
    resize() {
      const shadowElement = this.$refs.shadow as HTMLElement;
      const inputElement = this.$refs["number-input"] as HTMLInputElement;
      shadowElement.innerText = this.value.toString();
      inputElement.style.width = `${shadowElement.clientWidth}px`;
    },
    increment() {
      this.value += this.delta;
      this.resize();
    },
    decrement() {
      this.value -= this.delta;
      this.resize();
    },
  },
  watch: {
    // Watch for external changes
    modelValue(val) {
      this.value = parseFloat(val);
      this.resize();
    },

    value(val) {
      if (!this.value) this.value = 0;
      else this.value = parseFloat(String(val));

      // Hack to prevent leading zeros
      (this.$refs["number-input"] as HTMLInputElement).value = String(this.value);
      this.$emit("update:modelValue", this.value);
    },
  },
});
